}



.element-outer{
    background-color:#d6d6d6;
    height: 89vh;
    padding: 30px;
  }  
  

.element {
    align-items: center;
    box-shadow: 5px 5px 10px #b6b6b6,-5px -5px 10px #f6f6f6;

    /* box-shadow: 12px 12px 24px 0 rgba(0, 0, 0, 0.2),
      -12px -12px 24px 0 rgba(255, 255, 255, 0.5); */
    border-radius: 10px;
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 1.5rem;
    font-size: 1.5rem;
    margin: 20px auto;
    width: 300px;
    background: #d6d6d6;
  }

.element-btn{
  border-radius: 10px;
  background: #d6d6d6;
  box-shadow: 5px 5px 10px #b6b6b6,-5px -5px 10px #f6f6f6;

  padding: 1.5rem;
  
  margin: 30px auto;
  
}

